.container {
    padding-right: 20px;
    padding-left: 20px;
    margin: 0 auto;
  
    @media screen and (min-width: 320px) {
      width: 320px;
    }
  
    @media screen and (min-width: 768px) {
      padding-right: 32px;
      padding-left: 32px;
      width: 768px;
    }
  
    @media screen and (min-width: 1024px) {
      width: 1024px;
    }
  }